@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Sans);
body {
  padding: 0;
  overflow-y: auto;
}

div {
  font-family: 'IBM Plex Sans', sans-serif;
  overflow-y: hidden;
  overflow-x: hidden;
}

h1 {
  font-family: 'IBM Plex Sans', sans-serif;
}

:root {
    --borderSize: 3px; 
}

/* Regular Github */
.github-background { 
    background: #ffffff; 
    border: 3px solid #196127; 
    border: var(--borderSize) solid #196127;
}
.react-calendar-heatmap .github-color-scale-0 { fill: #ebedf0; }
.react-calendar-heatmap .github-color-scale-1 { fill: #c6e48b; }
.react-calendar-heatmap .github-color-scale-2 { fill: #7bc96f; }
.react-calendar-heatmap .github-color-scale-3 { fill: #239a3b; }
.react-calendar-heatmap .github-color-scale-4 { fill: #196127; }

/* Dracula */
.dracula-background { 
    background: #181818; 
    border: 3px solid #ff79c6; 
    border: var(--borderSize) solid #ff79c6;
}
.react-calendar-heatmap .dracula-color-scale-0 { fill: #282a36; }
.react-calendar-heatmap .dracula-color-scale-1 { fill: #44475a; }
.react-calendar-heatmap .dracula-color-scale-2 { fill: #6272a4; }
.react-calendar-heatmap .dracula-color-scale-3 { fill: #bd93f9; }
.react-calendar-heatmap .dracula-color-scale-4 { fill: #ff79c6; }

/* Hallowwen - Only appears on Halloween */
.halloween-background { 
    background: #ffffff; 
    border: 3px solid #FE9600; 
    border: var(--borderSize) solid #FE9600;
}
.react-calendar-heatmap .halloween-color-scale-0 { fill: #ebedf0; }
.react-calendar-heatmap .halloween-color-scale-1 { fill: #FFEE4A; }
.react-calendar-heatmap .halloween-color-scale-2 { fill: #FFC501; }
.react-calendar-heatmap .halloween-color-scale-3 { fill: #FE9600; }
.react-calendar-heatmap .halloween-color-scale-4 { fill: rgb(255, 145, 0); }

/* Teal */
.teal-background {
    background: #ffffff;
    border: 3px solid #458B74;
    border: var(--borderSize) solid #458B74;
}
.react-calendar-heatmap  .teal-color-scale-4 { fill: #458B74 }
.react-calendar-heatmap  .teal-color-scale-3 { fill: #66CDAA }
.react-calendar-heatmap  .teal-color-scale-2 { fill: #76EEC6 }
.react-calendar-heatmap  .teal-color-scale-1 { fill: #7FFFD4 }
.react-calendar-heatmap  .teal-color-scale-0 { fill: #ebedf0 }


/* YlGnBu */
.YlGnBu-background {
    background: #ffffff;
    border: 3px solid #253494;
    border: var(--borderSize) solid #253494;
}
.react-calendar-heatmap  .YlGnBu-color-scale-4 {fill: #253494 }
.react-calendar-heatmap  .YlGnBu-color-scale-3 {fill: #2c7fb8 }
.react-calendar-heatmap  .YlGnBu-color-scale-2 {fill: #41b6c4 }
.react-calendar-heatmap  .YlGnBu-color-scale-1 {fill: #a1dab4 }
.react-calendar-heatmap  .YlGnBu-color-scale-0 {fill: #ebedf0 }


.blue-background {
    background: #181818;
    border: 3px solid #4F83BF;
    border: var(--borderSize) solid #4F83BF;
}

.react-calendar-heatmap .blue-color-scale-4 { fill: #4F83BF}
.react-calendar-heatmap .blue-color-scale-3 { fill: #416895}
.react-calendar-heatmap .blue-color-scale-2 { fill: #344E6C}
.react-calendar-heatmap .blue-color-scale-1 { fill: #263342}
.react-calendar-heatmap .blue-color-scale-0 { fill: #222222}
h1.title {
  font-weight: bolder;
  font-family: 'IBM Plex Sans', sans-serif;
  font-size: 60px;
}

h1.typing-text {
  font-family: 'IBM Plex Sans', sans-serif;
  font-size: 30px;
  color: "#00ff00";
}

.card-text {
  font-size: 15px;
  color: black;
}

.card-title {
  font-size: 20px;
  
}


.header-color {
  background: #fff;
  color: #fff;
}

.landing-grid {
  background: #c5c5c5;
  text-align: center;
